.catalog__item--wrapper {
  display: flex;
  flex-wrap: wrap;
}

.catalog__item--info {
  flex-direction: column;
}

.catalog__title {
  position: relative;
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 16px;
  padding-bottom: 28px;
  font-size: 36px;
  line-height: 60px;

  &::before {
    content: "";

    position: absolute;
    top: 0;
    left: -20px;

    width: 320px;
    height: 1px;
    background: $c-mouse-gray;
  }

  &::after {
    content: "";

    position: absolute;
    bottom: 0;
    left: -20px;

    width: 320px;
    height: 1px;
    background: $c-mouse-gray;
  }
}

.catalog__item {
  position: relative;
  display: inline-block;
  width: 280px;
  padding-bottom: 25px;
}

.catalog__item::after {
  content: "";

  position: absolute;
  bottom: 0;
  left: -20px;

  width: 320px;
  height: 1px;
  background: $c-catalog-gray;
}

.catalog__item-image {
  width: 140px;
  height: 144px;
}

.catalog__list {
  width: 140px;
}

.catalog__value,
.catalog__field {
  width: 50%;
  vertical-align: top;
}

.catalog__value {
  padding-right: 0;

  text-align: right;
}

.catalog__field {
  padding-left: 0;

  text-align: left;
}

.catalog__item-title,
.catalog-to-all__title {
  font-size: 16px;
  line-height: 20px;
  color: $c-black;
  text-transform: uppercase;
}

.catalog__item-title {
  margin-top: 22px;
  margin-bottom: 11px;
  width: 75px;
}

.catalog__list,
.catalog-to-all__text {
  font-family: "Arial", "Helvetica", sans-serif;
  font-size: 12px;
  line-height: 12px;
  color: $c-darkgray;
  font-style: normal;
}

.catalog__order {
  line-height: 16px;
}

.catalog-to-all {
  position: relative;
  width: 280px;
  margin: 0 auto;
}

.catalog-to-all ::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: -20px;
  width: 320px;
  height: 1px;
  background: #ededed;
}

.catalog-to-all::after {
  content: "";
  position: absolute;
  top: 27px;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 30px;
  height: 30px;
  opacity: 0.2;
  background-image: url("../../img/icon-plus-mobile.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.catalog-to-all__title {
  display: block;
  margin-top: 80px;
  margin-bottom: 10px;

  text-align: center;
}

.catalog-to-all__text {
  display: block;
  margin-bottom: 20px;

  text-align: center;
}

.catalog-to-all__button {
  width: 280px;
  margin: 0 auto;
  padding-top: 11px;
  padding-bottom: 11px;

  color: $c-black;
  background-color: $c-lightgray;

  &:hover {
    background-color: $c-gray;
  }

  &:focus,
  &:active {
    opacity: 0.7;
  }
}

@media (min-width: $tablet-width) {
  .main__catalog {
    width: 100%;
  }

  .catalog {
    margin-left: -30px;
    margin-right: -30px;
  }

  .catalog__title {
    margin-top: 60px;
    margin-bottom: 30px;
    font-size: 60px;
    line-height: 60px;
  }

  .catalog__title::before,
  .catalog__title::after {
    display: none;
  }

  .catalog__item {
    width: 320px;
    max-width: 50%;
    margin: 0 30px 32px;
    padding: 110px 0 0;
    border: none;

    &::after {
      display: none;
    }
  }

  .catalog__item--wrapper {
    flex-direction: column;
    padding: 35px;
    padding-bottom: 40px;

    background-color: $c-lightgray;
  }

  .catalog__item-image {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    text-align: center;
    min-height: 213px;
    margin: -120px 0 0;
    width: 324px;
    height: 100%;
    transform: translateX(-11%);
    background-color: transparent;
  }

  .catalog__item-title {
    width: 127px;
    margin: 33px auto 16px;

    font-size: 20px;
    text-align: center;
  }

  .catalog__list {
    width: 250px;
    font-size: 16px;
  }

  .catalog__list td {
    padding-top: 9px;
    padding-bottom: 9px;
    border-bottom: 1px solid #cdcdcd;
  }

  .catalog__item--info {
    width: 250px;
    padding-bottom: 30px;
  }

  .catalog__order {
    width: 250px;
    margin: inherit;
  }

  .catalog-to-all {
    box-sizing: border-box;
    height: 100%;
    margin-top: 40px;
    padding-bottom: 40px;

    border: 1px solid $c-lightgray;
  }

  .catalog-to-all::after {
    background-image: url("../../img/icon-plus.svg");
  }

  .catalog-to-all__title {
    width: 140px;
    margin-left: 90px;
    font-size: 20px;
  }

  .catalog-to-all__text {
    width: 170px;
    margin-left: 75px;
    font-size: 16px;
  }

  .catalog-to-all__button {
    width: 250px;
  }
}

@media (min-width: $desktop-width) {
  .main__catalog {
    width: 1300px;
    margin-left: 40px;
    margin-right: 40px;
  }

  .catalog__title {
    margin-bottom: 65px;
    margin-top: 30px;
  }

  .catalog__item--wrapper {
    padding-left: 0;
  }

  .catalog__item-image {
    margin: -150px 17px 0;
  }

  .catalog__item {
    width: 240px;
    margin: 40px;
    border: none;
  }

  .catalog__list {
    width: 245px;
    padding-right: 35px;
    padding-left: 35px;
  }

  .catalog__order {
    width: 170px;
    margin-right: 35px;
    margin-left: 35px;
  }

  .catalog-to-all__title {
    margin-left: 40px;
  }

  .catalog-to-all__text {
    margin-left: 30px;
  }

  .catalog-to-all__button {
    width: 170px;
    margin-right: 35px;
    margin-left: 35px;
  }
}
